*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  font-family :'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
  box-shadow: 1px 0.5px 5px 1px;
}

.cart{
  margin-right:2.2rem;
}

.active{
  border-bottom: 2px solid black;
}


.list-group-item{
  background:rgba(218, 215, 237, 0.932);
  color: rgb(18, 14, 14);
  padding : 0.9rem;
  font-weight : 700;
  text-transform : uppercase;
}

.footer{
  font-size: 10px;
  background-color: rgba(128, 128, 128, 0.363);
  padding : 10px;
  line-height: 1px;
  text-decoration: none;
}
.footer p{
  margin-top: 0px;
  margin-bottom : 1px;
}

.footer a{
  text-decoration: none;
  color: black;
}

.register{
  margin: auto;
  background-color: rgba(8, 4, 32, 0.932);
  width : 50vh;
  height: 100%;
  padding:2rem;
  border-radius: 30px;
  margin-top: 2rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-transform: capitalize;
  font-weight: 600;
}

.register input{
  border-radius: 13px;
  background-color: rgba(255, 255, 255, 0.797);
  border: none;
  font-weight: 500;
}


.register button{
  width: 100%;
  border: none;
  margin-top: 2rem;
  border-radius: 30px;
  background-color: rgb(92, 92, 121);
}


.category{
  animation: both;
}

.carousel{
  margin: 2em auto;
  width: 98%;
  height: 525px;
}

.d-block{
  height: 525px;
}

.category-filter{
  background-color: rgba(99, 102, 103, 0.331);
  height: 100%;
  padding: 2rem;
  border-radius: 20px;
}